.main_container {
  justify-content: center;
}

.box {
  margin-top: 50px;
  border-top-left-radius: 17px;
  border-top-right-radius: 17px;
}



.box_one {
  background-color: rgb(61, 59, 62);
  margin-bottom: -15px;
  border-top-right-radius: 17px;
  border-top-left-radius: 17px;
}
.box_text h2 {
  color: white;
  padding-left: 20px;
  margin-top: 10px;
  font-family: "Times New Roman", Times, serif;
}

.box_text p {
  padding-left: 20px;
  margin-top: -18px;
  color: rgb(205, 207, 108);
}

.box_icon {
  padding-top: 35px;
}

.icon_color {
  color: rgb(253, 188, 26);
}

.second_box {
  border-top-right-radius: 17px;
  border-top-left-radius: 17px;
  background-color: black;
  border-radius: 17px;
  padding-top: 50px;
}

@media (max-width: 960px) {
    .bg-swap {
        
        height: 550px;
    }
}

@media (max-width: 414px) {
  #abc {
    margin-left: -5%;
  }
  .bg-swap {
    height: 730px;
  }
  .main_container {
    padding-top: 90px;
  }
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type="number"] {
  -moz-appearance: textfield;
}

